@use '../base/variables' as v;
@use '../base/mixins' as m;

.video {
    position: relative;
    max-height: 70rem;
    min-height: 30rem;
    overflow: hidden;

    .overlay {
        position: absolute;
        background: rgb(165,3,255);
        background: linear-gradient(270deg, rgba(165,3,255,0.5) 0%, rgba(255,0,110,0.3) 100%);
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        }
    video {
        width: auto;
        @include m.tablet {
            width:100%;
        }
        display: block;
    }
}
.contenido-video {
    z-index: 10;
    h2 {
        color: v.$blanco;
        font-size: 4rem;
        margin: 0;
    };
    p {
        background-color: v.$rosa;
        display: inline-block;
        padding: 0.5rem 0.5rem;
        color: v.$blanco;
        font-weight: 700;
        font-size: 1.7rem;
        margin: 0;
    }
}